import React, { useState, useEffect, useMemo } from 'react';
import { useParams, Link } from 'react-router-dom';
import useFetchData from '../hooks/useFetchData';
import { getProductImage } from '../utils/imageLoader';
import Breadcrumbs from '../components/common/Breadcrumbs'; // <-- Thm import
import './ProductDetailPage.css';

const PRICES_URL = 'https://raw.githubusercontent.com/nguyenthong123/dashboard-data/main/data/prices.json';
const PRODUCTS_URL = 'https://raw.githubusercontent.com/nguyenthong123/dashboard-data/main/data/products.json';

function ProductDetailPage() {
  const { slug } = useParams();
  const { data: prices, isLoading: isLoadingPrices } = useFetchData(PRICES_URL);
  const { data: products, isLoading: isLoadingProducts } = useFetchData(PRODUCTS_URL);
  const [mainImage, setMainImage] = useState(null);

  const productVariant = useMemo(() => {
    if (!prices) return null;
    return prices.find(p => p.product_slug === slug);
  }, [prices, slug]);

  const images = useMemo(() => {
    if (!productVariant || !productVariant["image sn phm"]) return [];
    return String(productVariant["image sn phm"])
      .split(',')
      .map(name => name.trim())
      .filter(Boolean)
      .map(name => getProductImage(name))
      .filter(Boolean);
  }, [productVariant]);

  useEffect(() => {
    if (images.length > 0) {
      setMainImage(images[0]);
    }
  }, [images]);

  if (isLoadingPrices || isLoadingProducts) {
    return <div className="page-container" style={{ textAlign: 'center', padding: '4rem 0' }}>ang ti thng tin sn phm...</div>;
  }

  if (!productVariant) {
    return (
      <div className="page-container" style={{ textAlign: 'center', padding: '4rem 0' }}>
        <h1 style={{ fontSize: '6rem', margin: 0, color: 'var(--primary-green)' }}>404</h1>
        <h2>Sn phm khng tn ti</h2>
        <p>Rt tic, chng ti khng tm thy sn phm bn yu cu.</p>
        <Link to="/" style={{ display: 'inline-block', marginTop: '1.5rem', padding: '10px 20px', backgroundColor: 'var(--primary-blue)', color: 'white', textDecoration: 'none', borderRadius: '6px' }}>
          Quay v Trang ch
        </Link>
      </div>
    );
  }

  const productGroup = products ? products[productVariant.group_id] : null;

 // --- TO D LIU CHO BREADCRUMBS ---
  const breadcrumbCrumbs = [
    { label: 'Trang ch', link: '/' },
    // Thm danh mc nu c
    ...(productGroup ? [{ label: productGroup.category, link: `/category/${productGroup.category.toLowerCase().replace(' ', '-')}` }] : []),
    // Thm tn sn phm hin ti
    { label: productVariant["Tn sn phm"], link: `/san-pham/${slug}` }
  ];


  return (
    <div className="page-container">
		 {/* --- THM BREADCRUMBS VO Y --- */}
      <Breadcrumbs crumbs={breadcrumbCrumbs} />
      <div className="product-detail-container">
        <div className="product-gallery">
          <div className="product-detail-image-wrapper">
            {mainImage ? (
              <img src={mainImage} alt={productVariant["Tn sn phm"]} className="product-detail-image" />
            ) : (
              <div>Loading image...</div>
            )}
          </div>
          {images.length > 1 && (
            <div className="thumbnail-container">
              {images.map((imgSrc, index) => (
                <div 
                  key={index} 
                  className={`thumbnail-item ${imgSrc === mainImage ? 'active' : ''}`}
                  onClick={() => setMainImage(imgSrc)}
                >
                  <img src={imgSrc} alt={`Thumbnail ${index + 1}`} />
                </div>
              ))}
            </div>
          )}
        </div>
        
        <div className="product-detail-info">
          {productGroup && <p style={{ color: 'var(--text-secondary-color)' }}>{productGroup.category}</p>}
          <h1>{productVariant["Tn sn phm"]}</h1>
          <div className="product-detail-price">
            {productVariant["Gi ch nh"]?.toLocaleString('vi-VN')} VN
          </div>
          <p>{productVariant.long_description || 'Cha c m t chi tit cho sn phm ny.'}</p>
          
          <h3 style={{ marginTop: '2.5rem' }}>Thng s k thut</h3>
          <table className="product-specs-table">
            <tbody>
              <tr><td>Kch thc</td><td>{productVariant["kch thc"]}</td></tr>
              <tr><td>S kg / tm</td><td>{productVariant["s kg trn tm"]}</td></tr>
              <tr><td>S tm / kin</td><td>{productVariant["s tm /kin"]}</td></tr>
            </tbody>
          </table>
        </div>
      </div>
      
      {productVariant.youtube_id && (
        <div style={{marginTop: '4rem'}}>
          <h2 style={{ textAlign: 'center' }}>Video Hng dn Thi cng</h2>
          <div className="video-wrapper" style={{ maxWidth: '800px', margin: '2rem auto', borderRadius: '12px' }}>
            <iframe
              style={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '100%', borderRadius: '12px' }}
              src={`https://www.youtube.com/embed/${productVariant.youtube_id}`}
              title={`Video for ${productVariant["Tn sn phm"]}`}
              frameBorder="0"
              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
              allowFullScreen
            ></iframe>
          </div>
        </div>
      )}
    </div>
  );
}

export default ProductDetailPage;